@import "../../common/styles/sass/variables";
@import "../../common/styles/sass/mixins";

.titleSection {
  padding: 80px 0;
  text-align: center;

  .title {
    @include fontStyle($fontFamily, 56px, $primaryColor);
    font-weight: 900;
    margin: 0;

    @media screen and (max-width: 728px) {
      font-size: calc(1rem + 5vw);
    }
  }
}





