@import "../../common/styles/sass/mixins";

.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 70px;
  background-color: rgba(37, 37, 37, 0.8);
  z-index: 1;

  .container {
    @include container();
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
  }
}